.banner{
    background-size: cover;
    color:white;
    
}
.content{
    padding-top:350px;
    height:190px;
    padding-left: 25px;
}
.movie-name{
    font-size: 3rem;
    font-weight: 700;
    padding-bottom:10px;
}
.button{
    margin-right: 1rem;
    color:white;
    border: none;
    outline: none;
    text-decoration: none;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51,51,51,0.6);
    cursor: pointer;
}
.button:hover{
    color:black;
    background-color: #e6e6e6;
}
.button:focus{ 
    outline: none; 
    color:#e6e6e6;
    background-color: rgba(51,51,51,0.3);
}
.movie-description{
    width:45rem;
    line-height: 1.3;
    padding-top: 1.5rem;
    font-size: 1.1rem;
    font-weight: 400;
    height:80px; 
    max-width: 600px;
}
.fade-bottom{
    height:25rem;
    background-image: linear-gradient(180deg,transparent,rgba(0, 0, 0, 0.2), #000) ;
}